.wrapper {
  height: 100%;
  background-color: #F7F7F7;
}
.scheduleServe {
  padding-top: 1.4rem;
  background: #f2f2f2;
}
.serveDetail {
  background: #f2f2f2;
  padding-bottom: 0.4rem;
}
.serveDetail .f2f2f2 {
  background: #f2f2f2;
}
.desc {
  color: #ccc;
  font-style: 14px;
}
.desc .people {
  color: #000;
  font-size: 14px;
}
.desc .tips {
  color: #ff953c;
  font-size: 14px;
}
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}
.body {
  background: #fff;
  padding: 8px 16px;
}
.body .title {
  width: 6rem;
}
.body .title .btn {
  float: right;
  color: #FF4463;
  font-weight: normal;
  border: 1px solid #FF4463;
  margin-right: 4px;
  padding: 0 8px;
  border-radius: 50px ;
}
.body .title .yelBtn {
  color: #f90;
  border: 1px solid #f90;
}
.fr {
  float: right;
}
.checkIcon {
  color: #349ce1;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #ccc;
  overflow: hidden;
  align-content: center;
  line-height: 20px;
  display: inline-block;
  margin-right: 3%;
  margin-left: 5%;
}
.labels label {
  width: 40%;
  display: inline-block;
}
.historyTop {
  background: #fff;
  padding: 8px 0;
  padding-left: 16px;
  font-size: 16px;
}
.historyTop .icon {
  color: #999;
  font-weight: normal;
  width: 0.64rem;
  margin-top: -8px;
}
.mb_8 {
  margin-bottom: 8px;
}
.mr_8 {
  margin-right: 8px;
}
.mt_8 {
  margin-top: 8px;
}
.mt_16 {
  margin-top: 16px;
}
.mt_2 {
  margin-top: 2px;
}
.detailFooter {
  text-align: center;
  background: #ff953c;
  line-height: 3;
  color: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  font-size: 16px;
  z-index: 100;
}
